
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import utils from '@/utils'
import api from '@/api'

export default {
name: 'DeviceMaintainDetail',
components: {
XHeader,
Swiper,
SwiperItem,
CheckIcon,
GroupTitle,
ViewBox
},
data () {
return {
demo01_index: 0,
demo01_list: [],
plan_id: 0,
items: [],
device: null
}
},
mounted () {
let self = this
self.plan_id = this.$route.params.id
console.log(self.plan_id)
if (self.plan_id > 0) {
let url = api.maintain + '/' + self.plan_id
utils.get(url, self.loadData)
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},
loadData (response) {
this.items = response.data.items
this.device = response.data.device
if (this.device.image_list && this.device.image_list.length > 0) {
for (var i = 0; i < this.device.image_list.length; i++) {
let img = {}
img.url = 'javascript:'
img.img = this.device.image_list[i]
this.demo01_list.push(img)
}
}
},
itemFinish (item) {
if (item.status === 1) {
return false
} else if (item.status === 2) {
return true
} else {
return false
}
}
}
}








