
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Rater from 'vux/src/components/rater/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import api from '@/api'
import utils from '@/utils'

export default {
name: 'DeviceRepairDetail',
directives: {
TransferDom
},
components: {
XHeader,
Swiper,
SwiperItem,
Rater,
ViewBox,
Confirm,
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
repair_id: '',
demo01_index: 0,
demo01_list: [],
repair: {},
rater_disabled: false,
stars: 0,
comments: [],
showAddComment: false,
reporter: {},
assignee: {},
reportinfo: {},
repairinfo: {},
score: {},
imgarrow: '',
error_msg: '',
confirm: false,
user: {},
can_star: false
}
},
mounted () {
this.repair_id = window.location.href.split('/repair-detail/')[1]
let self = this

self.axios
.get(api.repairDetail + this.repair_id)
.then(response => {
self.loadReportData(response.data.data)


})
.catch(error => {
console.log(error)
})
.finally()

self.getCommentList()

},
watch: {

stars: function (newStars, oldStars) {

if (this.can_star && newStars > 0 && newStars !== oldStars) {
this.scoreRepair()
}
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},
onSwiperItemIndexChange (index) {
console.log('demo item change', index)
},
onHide () {
console.log('on hide')
},
onShow () {
this.$refs.confirmComment.setInputValue('')
},
onCancel () {
},
onConfirm (value) {
this.confirm = false
},
showConfirm () {
this.confirm = true
setTimeout(() => {
this.confirm = false
}, 3000)
},
getCommentList () {
let self = this
self.axios
.get(api.repairComment + 'object_id=' + self.repair_id + '&object_type=' + '1')
.then(response => {

self.comments = response.data.data
})
.catch(error => {
console.log(error)
})
.finally()
},
loadReportData (report) {
this.repair = report
this.reportinfo = this.repair
this.repairinfo = this.repair.repair
this.reporter = this.repair.user
if (this.assignee) {
this.assignee = this.repair.assigneeinfo
}

this.can_star = true
if (this.repairinfo && this.repairinfo.score) {
this.score = this.repairinfo.score
if (this.score.score > 0) {
this.stars = this.score.score
this.can_star = false
}
}

for (var i = 0; i < this.reportinfo.image_list.length; i++) {
let image = {}
image.url = 'javascript:'
image.img = this.reportinfo.image_list[i]

this.demo01_list.push(image)
}
},
acceptReport () {
let posturl = api.acceptReport
let self = this
let fd = new FormData()
let token = window.localStorage.getItem('token')
fd.append('token', token)
fd.append('id', this.repair_id)

self.axios
.post(posturl, fd)
.then(function (response) {
if (response.data.code === 200) {
self.loadReportData(response.data.data)
} else if (response.data.code === 5009) {
utils.deletaUserData()
} else {
self.error_msg = response.data.code + ':' + response.data.message
self.showConfirm()
}
})
.catch(function (error) {
console.log(error)

})
.finally()
},
finishRepair () {

let self = this
let geturl = api.finishRepair + '?token=' + window.localStorage.getItem('token')
geturl += '&id=' + self.repairinfo.id

utils.get(geturl, function (response) {

self.loadReportData(response.data)
})
},
closeReport () {

let self = this
let geturl = api.closeReport + '?token=' + window.localStorage.getItem('token')
geturl += '&id=' + self.reportinfo.id

utils.get(geturl, function (response) {

self.loadReportData(response.data)
self.can_star = true
})
},
scoreRepair () {
let self = this

if (self.stars <= 0 || self.stars > 5) {
this.error_msg = '请填写正确的分数'
this.showConfirm()
return
}

let fd = new FormData()
let token = window.localStorage.getItem('token')
fd.append('token', token)
fd.append('id', self.repairinfo.id)
fd.append('type', 1)
fd.append('score', self.stars)
let posturl = api.score + '?token=' + window.localStorage.getItem('token')

utils.post(posturl, fd, function (response) {

console.log(response.data)
self.score = response.data
self.can_star = false
})
},
scoreConfim () {
this.error_msg = '不能重复评分！'
this.showConfirm()
},
clickAddComment () {
this.showAddComment = true
},
onConfirmComment (value) {
this.sendComment(value)
this.$refs.confirmComment.setInputValue('')
},
sendComment (message) {
let self = this
if (message === '') {
this.error_msg = '评论内容不能为空'
this.showConfirm()
return
}
let posturl = api.addcomment
let fd = new FormData()
let token = window.localStorage.getItem('token')
fd.append('token', token)
fd.append('id', this.repair_id)
fd.append('type', 1)
fd.append('content', message)

self.axios
.post(posturl, fd)
.then(function (response) {
if (response.data.code === 200) {

self.comments.unshift(response.data.data)
} else if (response.data.code === 5009) {
utils.deletaUserData()
} else {

self.error_msg = response.data.code + ':' + response.data.message
self.showConfirm()
}
})
.catch(function (error) {
console.log(error)

})
.finally()

}
}
}



